// 
const state = {
    //
    video: {
        /**
         * live
         */
        liveId: 0, //ID
        userId: 0, //ID
        roomId: 0, //ID
        liveCategoryCode: 0, //code
        liveCategoryName: "", //name
        liveTitle: "", //
        liveCover: "", //
        liveStatus: "", //:
        locationCityCode: "", //code
        locationCityName: "", //
        startDate: 0, //
        endDate: 0, //
        onlineCount: 0, //
        viewCount: 0, //
        replayUrls: [], //
        pushStreamUrl: "", //
        pullStreamUrl: "", //
        userInfo: {}, //
        liveConfig: {}, //
        /**
         * snscenter
         */
        followingCount: 0, //
        fansCount: 0, //
        ugcCount: 0 //UGC
    },
    //
    wonderfulList: {
        pageNo: 0, // 
        pageSize: 10, // 
        totalCount: 0, // 
        hasNext: true, // 
        list: []
    },
    //Ta
    replayList: {
        pageNo: 0, // 
        pageSize: 10, // 
        totalCount: 0, // 
        hasNext: true, // 
        list: []
    }
}

//mutation
// import {
//     SET_VIDEO_INFO,
//     ADD_WONDERFULL_LIST,
//     REMOVE_WONDERFULL_LIST,
//     ADD_REPLAY_LIST,
//     REMOVE_REPLAY_LIST
// } from '../mutation-types'
const SET_VIDEO_INFO = 'SET_VIDEO_INFO'
const SET_SNS_INFO = 'SET_SNS_INFO'
const ADD_WONDERFULL_LIST = 'ADD_WONDERFULL_LIST'
const REMOVE_WONDERFULL_LIST = 'REMOVE_WONDERFULL_LIST'
const ADD_REPLAY_LIST = 'ADD_REPLAY_LIST'
const REMOVE_REPLAY_LIST = 'REMOVE_REPLAY_LIST'

//  mutations
const mutations = {
    //video
    [SET_VIDEO_INFO](state, videoInfo) {
        state.video.liveId = videoInfo.liveId; //ID
        state.video.userId = videoInfo.userId; //ID
        state.video.roomId = videoInfo.roomId; //ID
        state.video.liveCategoryCode = videoInfo.liveCategoryCode; //code
        state.video.liveCategoryName = videoInfo.liveCategoryName; //name
        state.video.liveTitle = videoInfo.liveTitle; //
        state.video.liveCover = videoInfo.liveCover; //
        state.video.liveStatus = videoInfo.liveStatus; //:
        state.video.locationCityCode = videoInfo.locationCityCode; //code
        state.video.locationCityName = videoInfo.locationCityName; //
        state.video.startDate = videoInfo.startDate; //
        state.video.endDate = videoInfo.endDate; //
        state.video.onlineCount = videoInfo.onlineCount; //
        state.video.viewCount = videoInfo.viewCount; //
        state.video.replayUrls = videoInfo.replayUrls; //
        state.video.pushStreamUrl = videoInfo.pushStreamUrl; //
        state.video.pullStreamUrl = videoInfo.pullStreamUrl; //
        state.video.userInfo = videoInfo.userInfo; //
        state.video.liveConfig = videoInfo.liveConfig; //
    },
    //SNS
    [SET_SNS_INFO](state, snsInfo) {
        state.video.followingCount = snsInfo.followingCount
        state.video.fansCount = snsInfo.fansCount
        state.video.ugcCount = snsInfo.ugcCount
    },
    //
    [ADD_WONDERFULL_LIST](state, wonderfuls) {
        state.wonderfulList.pageNo = wonderfuls.pageNo
        state.wonderfulList.pageSize = wonderfuls.pageSize
        state.wonderfulList.totalCount = wonderfuls.totalCount
        state.wonderfulList.hasNext = wonderfuls.hasNext

        let list = wonderfuls.list
        for (var key in list) {
            if (state.wonderfulList.list.hasOwnProperty(key))
                continue
            state.wonderfulList.list.push(list[key])
        }
    },
    //
    [REMOVE_WONDERFULL_LIST](state) {
        if (state.wonderfulList.list) {
            state.wonderfulList.list = []
        }
    },
    //Ta
    [ADD_REPLAY_LIST](state, replays) {
        state.replayList.pageNo = replays.pageNo
        state.replayList.pageSize = replays.pageSize
        state.replayList.totalCount = replays.totalCount
        state.replayList.hasNext = replays.hasNext

        let list = replays.list
        for (var key in list) {
            if (state.replayList.list.hasOwnProperty(key))
                continue
            state.replayList.list.push(list[key])
        }
    },
    //Ta
    [REMOVE_REPLAY_LIST](state) {
        if (state.replayList.list) {
            state.replayList.list = []
        }
    }
}

export default {
    state,
    mutations
}



